:root {
  --bg: #070a0f;
  --panel: #0c111b;
  --stroke: #0f1726;
  --text: #e7f6ff;
  --muted: #9ee9ff;
  --neon: #00e5ff;
}

.terminal-wrap {
  width: 100%;
  max-width: none;
  height: 80vh;
  background: linear-gradient(180deg, #0a0f16 0%, #070a0f 100%);
  border: 1px solid rgba(0, 229, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 0 24px rgba(0, 229, 255, 0.12),
              inset 0 0 24px rgba(0, 229, 255, 0.06);
  display: grid;
  grid-template-rows: 40px 1fr 48px;
  overflow: hidden;
}

.terminal-head {
  background: #0b1018;
  border-bottom: 1px solid rgba(0, 229, 255, 0.15);
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0 14px;
}

.dot { width: 10px; height: 10px; border-radius: 50%; opacity: 0.9; }
.dot.red { background: #ff5f56; }
.dot.yellow { background: #ffbd2e; }
.dot.green { background: #27c93f; }

.title {
  margin-left: 8px;
  color: var(--muted);
  font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace;
  font-size: 13px;
}

/* Terminal Body - Desktop (preserve ASCII) */
.terminal-body {
  font-family: monospace;
  color: var(--text);
  padding: 14px 16px;
  overflow-y: auto;
  overflow-x: auto;
  line-height: 1.3;           /* tighter for ASCII */
  white-space: pre;           /* preserve spacing exactly */
  scrollbar-width: none;      /* Firefox hide */
  -ms-overflow-style: none;   /* IE/Edge hide */
}

/* Chrome, Safari, Edge hide */
.terminal-body::-webkit-scrollbar {
  display: none;
}

.line {
  text-shadow: 0 0 12px rgba(0, 229, 255, 0.4);
  white-space: pre;
  font-size: 14px;
  padding: 2px 0;
}

.terminal-input {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 12px 12px 12px;
  border-top: 1px solid rgba(0, 229, 255, 0.15);
}

.prompt {
  color: var(--neon);
  font-weight: 700;
  font-family: monospace;
}

.terminal-input input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: var(--text);
  font-family: inherit;
  font-size: 15px;
  caret-color: var(--neon);
  text-shadow: 0 0 8px rgba(0, 229, 255, 0.35);
}

/* Mobile Layout - wrapping enabled */
@media (max-width: 768px) {
  .terminal-body {
    white-space: pre-wrap;    /* wrap text on small screens */
    word-break: break-word;   /* break long words if needed */
  }
}

/* Adjust height for smaller laptops/tablets */
@media (max-height: 700px) {
  .terminal-wrap {
    height: 70vh;
  }
}


.terminal-shortcuts {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin: 8px 0;
}

.shortcut-btn {
  background: var(--panel);
  color: var(--neon);
  border: 1px solid var(--neon);
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.85rem;
  font-family: monospace;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
  text-shadow: 0 0 6px rgba(0, 229, 255, 0.4);
}

.shortcut-btn:hover {
  background: var(--neon);
  color: var(--bg);
  box-shadow: 0 0 8px var(--neon),
              0 0 16px var(--neon),
              0 0 32px rgba(0, 229, 255, 0.6);
  animation: neonPulse 1.2s infinite alternate;
}

@keyframes neonPulse {
  from {
    box-shadow: 0 0 6px var(--neon),
                0 0 14px var(--neon),
                0 0 28px rgba(0, 229, 255, 0.5);
  }
  to {
    box-shadow: 0 0 12px var(--neon),
                0 0 24px var(--neon),
                0 0 48px rgba(0, 229, 255, 0.8);
  }
}


.banner-line {
  text-align: center;      /* Center horizontally */
  display: block;          /* Ensure full line width */
  width: 100%;             /* Take full terminal width */
}