.shell {
    width: 100vw;
    height: 100vh;
    background: radial-gradient(1200px 800px at 20% 20%, rgba(0,229,255,0.08), transparent 60%),
                #070a0f;
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 24px;
    padding: 28px;
    box-sizing: border-box;
  }
  
  .left3d {
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: inset 0 0 120px rgba(0, 229, 255, 0.08);
    border: 1px solid rgba(0, 229, 255, 0.12);
  }
  
  .rightPanel {
    display: grid;
    grid-template-rows: auto 1fr;
    gap: 18px;
  }
  
  .brand h1 {
    margin: 0;
    letter-spacing: 2px;
    font-size: 40px;
    color: #eaf9ff;
    text-shadow: 0 0 18px rgba(0, 229, 255, 0.25);
  }
  
  .brand p {
    margin: 6px 0 0 0;
    color: #9ee9ff;
    font-size: 18px;
    opacity: 0.9;
  }
  

  .typing {
    font-family: "Courier New", Courier, monospace;
    font-size: 18px;
    color: var(--neon);
    white-space: nowrap;
    overflow: hidden;
  }
  
  .cursor {
    display: inline-block;
    margin-left: 2px;
    width: 2px;
    background: var(--neon);
    animation: blink 0.8s steps(1) infinite;
  }
  
  @keyframes blink {
    50% {
      opacity: 0;
    }
  }
  

@media (max-width: 768px) {
  .shell {
    display: flex;             
    flex-direction: column;
    gap: 12px;
    padding: 12px;
  }

  .rightPanel {
    order: 1;                 
    height: auto;              
  }

  .left3d {
    order: 2;                  
    height: 80vh;              
  }

  .left3d canvas {
    width: 100% !important;
    height: 100% !important; 
  }

  .brand h1 {
    font-size: 28px;           
  }

  .typing {
    font-size: 14px;           
  }

  .terminal-wrap {
    height: 45vh;             
    max-width: 100%;
  }
}